import React from 'react'
import './FormView.scss';
import './print.scss';
import axios from 'axios';

export default class FormView extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			data:[],
			selected:null,
			backend_url:'https://devhammed.000webhostapp.com/car_form_ajax.php',
		}
	}
	componentDidMount(){
		this.loadData();
	}
	loadData(){
		let data = axios.get(this.state.backend_url+'?data=1')
		.then(res => {
			// console.log(res);
			this.setState({data: res.data})
		})
		.catch(err => console.log(err));
	}
	renderFormOptions(){
		if(this.state.data.length > 0){
			return this.state.data.map(form => (
				<option key={form.id} id={form.id} name={form.full_name} value={form.id}>{form.full_name}</option>
			));
		}
	}
	renderFormDetails(){
		if(this.state.selected){
			let form = this.state.selected[0];
			return (
				<table>
					<tbody>
						<tr>
							<td>Full Name</td><td>{form.full_name}</td>
						</tr>
						<tr>
							<td>Tetra</td><td>{form.tetra_1}</td>
						</tr>
						<tr>
							<td>Company</td><td>{form.company}</td>
						</tr>
						<tr>
							<td>Supervisor Name:</td><td>{form.supervisor_name}</td>
						</tr>		
						<tr>
							<td>Tetra:</td><td>{form.tetra_2}</td>
						</tr>			
						<tr>
							<td>From:</td><td>{form.from_date}</td>
						</tr>			
						<tr>
							<td>Odemeter Reading:</td><td>{form.from_odemeter_reading}</td>
						</tr>			
						<tr>
							<td>To:</td><td>{form.to_date}</td>
						</tr>			
						<tr>
							<td>Odemeter Reading:</td><td>{form.to_odemeter_reading}</td>
						</tr>			
						<tr>
							<td>Asset Number:</td><td>{form.asset_number}</td>
						</tr>			
						<tr>
							<td>Signature Date:</td><td>{form.signature_date}</td>
						</tr>			
						<tr>
							<td>Signature:</td><td><img src={form.signature_pad}/></td>
						</tr>			
					</tbody>
				</table>
			)
		}else{
			return <div>^ Please Select a form to view ^</div>
		}
	}
	handleSelectChange = (e) => {
		let form_id = e.target.value;
		if(!isNaN(e.target.value)){
			let find_cust = this.state.data.filter( form => form.id === form_id)
			if(find_cust){
				this.setState({selected: find_cust});
			}else{
				console.log('form not found');
			}
		}
	}
	render() {
		let form_options = this.renderFormOptions();
		let form_details = this.renderFormDetails();

		return (
			<div className="wrapper">
				<select name="form" onChange={this.handleSelectChange}>
					<option>Choose a form</option>
					{form_options}
				</select>
				<button className="print_btn" onClick={() => window.print()}>Print</button>
				<hr/>
				<div className="print">
					{form_details}
				</div>
			</div>
			
		)
	}
}