import React from 'react'
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import SignaturePad from 'signature_pad';
import $ from 'jquery'

export default class Form extends React.Component {
	constructor(props){
		super(props);
		this.state = {	
			// data:[],
	    backend_url:'https://devhammed.000webhostapp.com/car_form_ajax.php',
			debug_backend_url: 'http://localhost:80/jalal/booking_form/api/car_form_ajax.php',
			car_form:1,
			driver: {
				full_name: '',
				supervisor_name:'',
				company: '',
				tetra1:'',
				tetra2:'',
			},
			booking: {
				from:new Date(),
				to:new Date(),
				odometer_reading:'',
				odemeter_reading:'',
				signature:'',
				signature_date:new Date(),
			},
			vehicle:{
				asset_number:'',
			},
			text:{
				pre_before_return: `Before Returning a Pool Vehicle Please ensure you do the following:`,
				post_before_return:`Note: Failure to comply with the above checklist could result in declining the pool vehicle in future.
				   *By signing this document, you agreeing to the terms and conditions of chevron.
				`
			},
			signaturePad:''
		}
	}
	componentDidMount(){
		let signaturePad;
    $(document).ready(() => {
      // this.loadData();
      var canvas = document.querySelector("canvas");
      var e = $.Event("keypress")
      signaturePad = new SignaturePad(canvas, {
        minWidth:1,
        maxWidth:2,
        onEnd: () => this.updateSignaturePad(signaturePad)
      });
      // this.setSignaturePad(signaturePad)
      //fill form 
      document.querySelector('input[name="full_name"]').value = 'example'
      document.querySelector('input[name="tetra1"]').value = 'example'
      document.querySelector('input[name="company"]').value = 'example'
      document.querySelector('input[name="supervisor_name"]').value = 'example'
      document.querySelector('input[name="tetra2"]').value = 'example'
      document.querySelector('input[name="from_odemeter_reading"]').value = 'example'
      document.querySelector('input[name="to_odemeter_reading"]').value = 'example'
      document.querySelector('input[name="asset_number"]').value = 1344

      // console.log(full_name);
    });
	}
	 //  loadData(){
  //   let data = axios.get(this.state.backend_url+'?data=1')
  //   .then(res => {this.setState({data: res.data})})
  //   .catch(err => console.log(err));
  // }
	handleSignatureDateChange = date => {
    this.setState(prevState => {
      return {
        ...prevState,
        booking: {
          ...prevState.booking,
          signature_date: date
        }
      }
    });
  }
  updateSignaturePad = (signaturePad) => {
    this.setState({signaturePad: signaturePad.toDataURL()});
  }
  clearCanvas(){
    const canvas = document.querySelector('.cc');
    const context = canvas.getContext('2d');
    context.clearRect(0, 0, canvas.width, canvas.height);
  }
	handleFromChange = date => {
		this.setState(prevState => {
		  return {
		    ...prevState,
		    booking:{
		      ...prevState.booking,
		      from:date
		    }
		  }
		});
	}

	handleToChange = date => {
		this.setState(prevState => {
		  return {
		    ...prevState,
		    booking:{
		      ...prevState.booking,
		      to:date
		    }
		  }
		});
	}

	render() {
		let form = (
			<form 
			method="post" 
			action={this.state.backend_url}>
			  <h3><u>Driver Details</u></h3>

			  <div className="group_field">
			    <div className="contain">
			      <span>Full Name:</span>&nbsp;
			      <input type="text" name="full_name"/>
			    </div>
			    <div className="contain">
			      <span>Tetra:</span>&nbsp;
			      <input type="text" name="tetra1"/>
			    </div>
			  </div>

			  <div className="contain">
			    <span>Company:</span>&nbsp;&nbsp;
			    <input type="text" name="company"/>
			  </div>

			  <div className="group_field">
			    <div className="contain">
			      <span>Supervisor Name:</span>&nbsp;
			      <input type="text" name="supervisor_name"/>
			    </div>
			    <div className="contain">
			      <span>Tetra:</span>&nbsp;
			      <input type="text" name="tetra2"/>
			    </div>
			  </div>

			  <h3><u>Booking Details</u></h3>
			  <div className="group_field">
			    <div className="contain">
			      <span>From:</span>&nbsp;
			      <DatePicker
			        name="from_date"
			        selected={this.state.booking.from}
			        onChange={this.handleFromChange}/>
			    </div>
			    <div className="contain">
			      <span>Odemeter reading:</span>&nbsp;
			      <input type="text" name="from_odemeter_reading"/>
			    </div>
			  </div>
			  <div className="group_field">
			    <div className="contain">
			      <span>To:</span>&nbsp;
			      <DatePicker
			        name="to_date"
			        selected={this.state.booking.to}
			        onChange={this.handleToChange}/>
			    </div>
			    <div className="contain">
			      <span>Odemeter reading:</span>&nbsp;
			      <input type="text" name="to_odemeter_reading"/>
			    </div>
			  </div>

			  <h3><u>Vehicle Details</u></h3>
			    <div className="contain">
			      <span>Asset Number:</span>&nbsp;
			      <input type="text" name="asset_number"/>
			    </div>
			  {this.state.text.pre_before_return}
			  <ol>
			    <li>-  Fill the fuel tank</li>
			    <li>-  Ensure cleaning the car</li>
			    <li>-  Park the car in the designated pool vehicle parking and isolate it.</li>
			    <li>-  Return the key to pool office upstairs.</li>
			  </ol>
			  {this.state.text.post_before_return}
			  <br/><br/>
			  <div className="group_field">
			      <span>Signature: </span>&nbsp;  
			    <div className="contain">
			      <canvas style={{border:'1px solid grey'}} className="cc"></canvas>
			      <span onClick={() => this.clearCanvas()}>X</span>
			    </div>
			    &nbsp;&nbsp;
			    <div className="contain">
			      <span>Date:</span>&nbsp;
			      <DatePicker
			          name="signature_date"
			          selected={this.state.booking.signature_date}
			          onChange={this.handleSignatureDateChange}/>
			    </div>
			  </div>
			  <input type="hidden" name="signaturePad" value={this.state.signaturePad}/>
			  <input style={{float:'right'}} type="submit" name="carForm" value="Book"/>
			</form>
    	);
		return (
			<div>
				{form}	
			</div>
		)
	}
}